<template>
    <div class="Table">
        <el-button type="primary" @click="exportTable()"></el-button>
        <el-table :data="tableData" :border="'border'" id="selectTable" style="width: 100%">
            <el-table-column prop="date" label="" width="180"></el-table-column>
            <el-table-column prop="name" label="" width="180"></el-table-column>
            <el-table-column prop="address" label=""></el-table-column>
        </el-table>
    </div>
</template>

<script>
import { Toast } from 'vant';
import toExcel from '@/utils/excel'
export default {
    name: 'Table',
    data() {
        return {
            tableData: [{
                date: '2016-05-02',
                name: '',
                address: ' 1518 '
            }, {
                date: '2016-05-04',
                name: '',
                address: ' 1517 '
            }, {
                date: '2016-05-01',
                name: '',
                address: ' 1519 '
            }, {
                date: '2016-05-03',
                name: '',
                address: ' 1516 '
            }],
        }
    },
    computed: {
        
    },
    methods: {
        exportTable() {
            toExcel.getExcel('#selectTable','test')
        },
        /**
         * @description 
         * @param { Array } arrsy 
         * @param { number } size  
         */
        chunkArrayFun(array, size) {
            let arr = [];
            let index = 0;

            for (let i = 0; i < array.length; i += size) {
                let chunkArr = [];

                for (let j = 0; j < size; j++) {
                    chunkArr[j] = array[index++];
                    if (index == array.length) break;
                }
                arr.push(chunkArr);
            }
            return arr;
        },
    },
    async mounted () {
        let date1 = new Date().getTime()
        let data = []
        for (let i = 0; i < 40000; i++) {
            data.push({
                date: '2016-05-03',
                name: '',
                address: ' 1516 '
            })
        }
        let arr = this.chunkArrayFun(data, 600);
        for (let i = 0; i < arr.length; i++) {
            setTimeout(() => {
                this.tableData.push(...arr[i]);
            }, 10);
        }
        this.$nextTick(() => {
            console.log(new Date().getTime() - date1)
        })
    }
}

</script>

<style lang="scss">
    .Table {
        width: 100%;
        height: 100%;
        padding: .1rem;
    }
</style>


